.sliderWidget {
  font-size: 1em;
}
.sliderWidget .output, .sliderWidgetPanel {
  margin: 0 1em;
}
.sliderWidget .output {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sliderWidget .outputText {
  font-weight: bold;
}
.sliderWidget .outputResult {
  background-color: #ffffff;
  border-radius: 0.5em;
  padding: 0.5em 1em;
  gap: 0.5em;
  display: flex;
}
.sliderWidget .outputResultSign {
  color: #848484;
}
.sliderWidget .outputResult :global .sign {
  color: #848484;
}